@use "../setting" as *;

$initType: 'normal';
@mixin button($type:$initType){

  //共通スタイル

  //サイト共通ボタンのスタイル1
@if( $type == 'reserve' ){
    //スタイル
    box-sizing: border-box;
    min-width: 103px;
    background-color: $global;
    color: #ffffff;
    font-weight: bold;
    padding: 19px 37px;

}

  //サイト共通ボタンのスタイル2
@else if( $type == 'link' ){
    //スタイル
}

}


